<template>
  <div>
     <div class="navbar" role="navigation">
      <div class="navbar-header">
        <div class="logo">
          <h1></h1>
        </div>
      </div>
      <div class="pull-right nav-right">
        <label>
          
          <i>{{id}}</i>
        </label>
        <a href="javascript:;" @click="dialogFormVisible1=true">
          <i class="iconfont icon-tuichudenglu"></i>
        </a>
      </div>
    </div>
	<div class="template-page-wrapper" id="box">
		<div class="navbar-collapse collapse templatemo-sidebar">
			<ul class="templatemo-sidebar-menu">
				<li class="welcomeBack"> <i>{{id}}</i></li>
				<li class="active"><router-link to="index_build"><i class="iconfont icon-zhuye"></i></router-link></li>
				<li class="sub">
					<a href="javascript:;">
						<i class="iconfont icon-sushe"></i>  <div class="pull-right"><span class="caret"></span></div>
					</a>
					<ul class="templatemo-submenu">
						<!---<li><router-link to="fangjian_build"></router-link></li>--->
						<li><router-link to="chuangwei_build"></router-link></li>
					</ul>
				</li>
				<li class="sub" >
					<a href="javascript:;">
						<i class="iconfont icon-xue"></i>  <div class="pull-right"><span class="caret"></span></div>
					</a>
					<ul class="templatemo-submenu">
						<li><router-link to="jiluweiji"></router-link></li>
						<li><router-link to="jlweiji"></router-link></li>
					</ul>
					<ul class="templatemo-submenu">
						<li><router-link to="fangkexinxi"></router-link></li>
						<li><router-link to="jlfangkexinxi"></router-link></li>
					</ul>
				</li>
				<li class="sub" >
					<a href="javascript:;">
						<i class="iconfont icon-xitongrizhi"></i>  <div class="pull-right"><span class="caret"></span></div>
					</a>
					<ul class="templatemo-submenu">
						<li><router-link to="xiaotz"></router-link></li>
					</ul>
					<ul class="templatemo-submenu">
						<li><router-link to="xuetz"></router-link></li>
						<li><router-link to="fbxuetz"></router-link></li>
					</ul>
				</li>
				<li>
					<a href="javascript:;" data-toggle="modal" data-target="#confirmModal">

					</a>
				</li>
			</ul>
		</div>

      <div class="templatemo-content-wrapper">
        <div class="templatemo-content">
          <router-view></router-view>
        </div>
      </div>
      <!-- Modal -->
      <el-dialog title="" :visible.sync="dialogFormVisible1" width="20%">
        <el-form class="dialog">
          <span style="margin-left:80px;font-size:20px;font-weight:500"></span>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible1 = false"> </el-button>
          <el-button type="primary" @click="clearUser"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  name: "nav_build",
  data() {
    return {
      id: this.COMMON.id,
      dialogFormVisible1:false
    };
  },
  methods: {
    clearUser() {
      this.COMMON.clearUser();
      this.$router.replace('/NewLogin')
    }
  },
  mounted: function() {
    $(".templatemo-sidebar-menu li.sub a").click(function() {
      if (
        $(this)
          .parent()
          .hasClass("open")
      ) {
        $(this)
          .parent()
          .removeClass("open");
      } else {
        $(this)
          .parent()
          .addClass("open");
      }
    });
  }
};
</script>

<style scoped>
.temp {
  margin-top: 15%;
}
</style>



