<template>
  <div class="all">
    <div class="box1">
      <div class="box2">
        <div class="title"></div>
        <el-form :rules="loginFormRules" label-width="100px" class="form" :model="loginForm">
            <el-form-item label="" prop="region">
              <el-select v-model="loginForm.status" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="username" class="">
              <el-input  v-model="loginForm.username" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password" class="">
              <el-input type="password" v-model="loginForm.password" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <div class="login-button">
          <el-button type="primary" @click="login"></el-button>
        </div>
        <div class="forget-psw">

          <div class="" align="center" @click="forget"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  export default {
    name: 'NewLogin',
    data () {
      var checkUserName = (rule, value, callback) => {
        console.log(value);
        if (!value) {
          return callback(new Error(''));
        }
        callback();
      };
      var checkUserPsw = (rule, value, callback) => {
        console.log(value);
        if (!value) {
          return callback(new Error(''));
        }
        callback();
      };
      return {
        loginForm: {
          username: '',
          password: '',
          status: ''
        },
        loginFormRules: {
          username: [{ validator: checkUserName, trigger: 'blur' }],
          password: [{ validator: checkUserPsw, trigger: 'blur'}]
        },
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }],
        responseResult: [],
        value:''
      }
    },
    methods: {
      //methods  /login 
      // /index URL http://localhost:8443/api/login
      login () {
        console.log(this.loginForm),
        this.$axios
          .post('/login', {
            id: this.loginForm.username,
            psw: this.loginForm.password,
            status: this.loginForm.status.toString(),
          })
          //successResponse
          //successResponsejsonHttpServletResponse
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              this.COMMON.id=this.loginForm.username,
                this.COMMON.status=this.loginForm.status.toString()
                if(this.loginForm.status.toString() === '1'){
                  this.$router.replace({path: '/nav_student/info'})//
                }
                else if(this.loginForm.status.toString() === '2'){
                  this.$router.replace({path: '/nav_build/index_build'})//
                }
                else if(this.loginForm.status.toString() === '3'){
                  this.$router.replace({path: '/nav_manager/index'})//
                }
                else{

                }
            }
            if (successResponse.data.code === 400) {
              //this.$router.replace({path: '/StudentLogin'})
              this.$alert("")
            }
          })
          //
          .catch(failResponse => {
          })
      },
      forget(){
        alert("")
      }
    }
  }
</script>

<style scoped>
  .all{

    position: absolute;
    min-height: 100%;
    height: auto;
    overflow:hidden;
    width:100%;

    background-repeat: no-repeat;
    background-size:100% 100%;
    background-attachment:fixed;
    background-image:url(https://www.bupt.edu.cn/images/18/05/09/1mu9mjraxf/xyfg3.jpg);
  }
  .box1{
    height: 300px;
    position:relative;
  }
  .box2{
    border-radius:20px;
    position: absolute;
    width: 400px;
    height:450px;
    //border: 1px solid red;
    background-color: #F7F7F7;
    left:50%;
    margin-left: -200px;
    margin-top: 170px;
    display: flex;
    flex-direction: column;
    align-items: center;
    //justify-content:center;
  }
  .title{
    //border:1px solid green;
    font-size: 24px;
    margin-top: 60px;
    //margin:0 auto;
  }
  .form{
    margin-top: 30px;
  }
  .login-button{
    margin-top: 0px;
  }
  .forget-psw{
    margin-top: 20px;
  }

</style>



