<template>
  <div class="addpadding">
    <label class="pull-left" style="margin: 20px 0;">
      
      <input
        type="text"
        style="border: #ccc solid 1px;border-radius: 4px;"
        v-model="keywords"
      />
    </label>
    <div class="tableTop">
      <el-form :inline="true" class="pull-right">
        <el-form-item>
          <el-button type="primary"  @click="dialogFormVisible = true" ></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="success"  @click="dialogFormVisible1 = true" ></el-button>
        </el-form-item>
      </el-form>
    </div>
    <table class="table table-bordered table-hover table-striped">
      <thead>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="item in search(keywords)" :key="item.id">
        <td>{{ item.name }}</td>
        <td>{{ item.id }}</td>
        <td>{{ item.sex }}</td>
        <td>{{ item.major }}</td>
        <td>{{ item.yearIn }}</td>
        <td>
          <li style="list-style: none;">
            <button type="reset" class="btn btn-success" @click="modify(item.id,item.name,item.sex,item.major,item.yearIn)"></button>
            <button type="reset" class="btn btn-danger" @click="del(item.id)"></button>
          </li>
        </td>
      </tr>
      </tbody>
    </table>

    <el-dialog title=""
               :visible.sync="dialogFormVisible"
               width="40%">
      <el-form class="dialog" label-position="right" label-width="30%">
        <el-form-item label=":">
          <el-col :span="14">
            <el-input v-model="id" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label=":">
          <el-col :span="14">
            <el-input v-model="name" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label=":">
          <el-col :span="14">
            <el-input v-model="sex" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label=":">
          <el-col :span="14">
            <el-input v-model="major" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label=":">
          <el-col :span="14">
            <el-input v-model="year" placeholder=""></el-input>
          </el-col>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="add"> </el-button>
          </span>
    </el-dialog>

    <el-dialog title=""
               :visible.sync="dialogFormVisible1"
               width="40%">
      <el-form class="dialog"
               label-position="right"
               label-width="30%">
        <el-form-item label=":">
          <el-col :span="14">
            <el-input v-model="id2"></el-input>
          </el-col>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible1 = false"> </el-button>
        <el-button type="primary" @click="reset"> </el-button>
      </div>
    </el-dialog>

    <el-dialog title=""
               :visible.sync="dialogFormVisible2"
               width="40%">
      <el-form class="dialog" label-position="right" label-width="30%">
        <el-form-item label=":">
          <el-col :span="14">
            <el-input v-model="mid" :disabled="true"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label=":">
          <el-col :span="14">
            <el-input v-model="mname"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label=":">
          <el-col :span="14">
            <el-input v-model="msex"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label=":">
          <el-col :span="14">
            <el-input v-model="mmajor"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label=":">
          <el-col :span="14">
            <el-input v-model="myear"></el-input>
          </el-col>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible2 = false"> </el-button>
            <el-button type="primary" @click="modifySub"> </el-button>
          </span>
    </el-dialog>

  </div>
</template>

<script>
  export default {
    name: "xiaoqu",
    data() {
      return {
        keywords:"",
        id: "",
        name: "",
        sex:"",
        major:"",
        year:"",

        mid:"",
        mname:"",
        msex:"",
        mmajor:"",
        myear:"",

        id2:"",
        ids:[],
        dialogFormVisible1: false,
        dialogFormVisible: false,
        dialogFormVisible2: false,
        list:[],
        submited:true,
      };
    },
    mounted() {
      this.get_student();
    },
    methods: {
      get_student() {
        this.$axios
          .get("/manager/get_student")
          .then(successResponse => {
            this.list = successResponse.data;
            for(var i=0;i<this.list.length;i++){
              this.ids.push(this.list[i].id);
            }
          })
          .catch(failResponse => {
            this.$message({
              message:'',
              type:'error',
            });
          });
      },
      reset() {
        if (this.id2 === "") {
          this.$message({
            message:'',
            type:'warning',
          });
          this.submited=false;
        } else if(!this.ids.includes(this.id)){
          this.$message({
            message:'',
            type:'warning',
          });
          this.submited=false;
        }

        if(!this.submited){
          this.submited=true;
          return false;
        }else{
          this.$axios
            .post("/manager/reset_psw", {
              id: this.id2,
            })
            .then(successResponse => {
              this.id2="";
              this.$message({
                message:'',
                type:'success',
              });
            })
            .catch(failResponse => {
              this.$message({
                message:'',
                type:'error',
              });
            });
        }
        this.dialogFormVisible1 = false;
      },
      add(){
        if (this.id === "") {
          this.$message({
            message:'',
            type:'warning',
          });
          this.submited=false;
        } else if(this.ids.includes(this.id)){
          this.$message({
            message:'',
            type:'warning',
          });
          this.submited=false;
        } else if(this.name === ""){
          this.$message({
            message:'',
            type:'warning',
          });
          this.submited=false;
        }else if(this.sex===""){
          this.$message({
            message:'',
            type:'warning',
          });
          this.submited=false;
        }else if(this.major === ""){
          this.$message({
            message:'',
            type:'warning',
          });
          this.submited=false;

        }else if(this.year === ""){
          this.$message({
            message:'',
            type:'warning',
          });
          this.submited=false;

        }

        if(!this.submited){
          this.submited=true;
          return false;
        }else{
          this.$axios
            .post("/manager/add_student", {
              id: this.id,
              name: this.name,
              sex:this.sex,
              major:this.major,
              yearIn:this.year,
              psw:this.id,
            })
            .then(successResponse => {
              this.get_student();
              this.$message({
                message:'',
                type:'success',
              });
            })
            .catch(failResponse => {
              this.$message({
                message:'',
                type:'error',
              });
            });
        }
        this.dialogFormVisible = false;
      },
      del(id) {
        this.$confirm('','',{
          confirmButtonText:'',
          cancelButtonText:'',
          type:'warning',
        }).then(()=>{
          this.$axios
            .post("/manager/del_student",{id:id})
            .then(successResponse => {
              this.get_student();
              this.$message({
                message:'',
                type:'success',
              });
            })
            .catch(failResponse => {
              this.$message({
                message:'',
                type:'error',
              });
            });
        })
          .catch(()=>{
          })
      },
      modify(id,name,sex,major,year) {
        this.dialogFormVisible2=true;
        this.mid=id;
        this.mname=name;
        this.msex=sex;
        this.mmajor=major;
        this.myear=year;
      },
      modifySub(){
        if(!this.submited) {
          this.submited = true;
          return false;
        }
        else{
          this.$confirm('','',{
            confirmButtonText:'',
            cancelButtonText:'',
            type:'warning',
          }).then(()=>{
            this.$axios
              .post("/manager/modify_student",{
                id:this.mid,
                name:this.mname,
                sex:this.msex,
                major:this.mmajor,
                yearIn:this.myear,
              })
              .then(successResponse => {
                this.get_student();
                this.$message({
                  message:'',
                  type:'success',
                });
                this.dialogFormVisible2=false;
              })
              .catch(failResponse => {
                this.get_student();
                this.$message({
                  message:'',
                  type:'error',
                });
              });
          })
            .catch(()=>{
            });
        }
      },
      search(keywords) {
        return this.list.filter(item => {
          if (
            item.name.includes(keywords) ||
            item.id.toString().includes(keywords)
          ) {
            return item;
          }
        });
      },
    }
  };
</script>

<style scoped>
  .dialog{
    text-align: center;
    margin: auto;
  }
  .dialog label{
    font-weight: 400;
    font-size: 20px;
    margin-right: 10px;
  }
  .dialog input{
    width: 200px;
    height: 23px;
  }
</style>



